<template>
    <div class="body">
        <div class="bigBox" v-if="cvInfo">
            <div class="header">
                <div class="name">{{ cvInfo.name }}</div>
                <div class="phone">{{ cvInfo.phone }}</div>
                <div class="com">{{ cvInfo.email }}</div>
                <div class="star"><i class="el-icon-star-on" @click="changeyColor"
                        :style="{ color: cvInfo.isCollect == 1 ? 'rgba(255, 235, 59, 1)' : 'gray' }"></i></div>
            </div>
            <div class="choice">
                <span
                    :style="[{ color: bcolor == 0 ? 'rgba(47, 84, 235, 1)' : 'black' }, { borderBottom: bcolor == 0 ? '5px solid rgba(47, 84, 235, 1)' : '' }]"
                    @click="changebColor0">解析结果</span>
                <span
                    :style="[{ color: bcolor == 1 ? 'rgba(47, 84, 235, 1)' : 'black' }, { borderBottom: bcolor == 1 ? '5px solid rgba(47, 84, 235, 1)' : '' }]"
                    @click="changebColor1">简历画像</span>
                <span
                    :style="[{ color: bcolor == 2 ? 'rgba(47, 84, 235, 1)' : 'black' }, { borderBottom: bcolor == 2 ? '5px solid rgba(47, 84, 235, 1)' : '' }]"
                    @click="changebColor2">原始文本</span>
            </div>
            <div class="result" :style="{ display: bcolor == 0 ? 'block' : 'none' }">
                <div class="basicInfo">
                    <div class="background"></div>
                    <div class="basic">基本信息</div>
                    <div><span style="color:gray">姓名：</span>{{ cvInfo.name }}</div>
                    <div><span style="color:gray">手机：</span>{{ cvInfo.phone }}</div>
                    <div><span style="color:gray">邮箱：</span>{{ cvInfo.email }}</div>
                    <div><span style="color:gray">地址：</span>{{ cvInfo.address }}</div>
                    <div class="twoPart">
                        <div><span style="color:gray">年龄：</span>{{ cvInfo.age }}</div>
                        <div><span style="color:gray">民族：</span>{{ cvInfo.nation }}</div>
                        <div><span style="color:gray">政治面貌：</span>{{ cvInfo.politicalStatus }}</div>
                        <div><span style="color:gray">求职意向：</span>{{ cvInfo.career }}</div>
                    </div>
                </div>
                <div class="eduEx">
                    <div class="background"></div>
                    <div class="basic">教育经历</div>
                    <div class="ex1">
                        <div>学校名称|学历|专业名称: {{ cvInfo.schoolExperience }},{{ cvInfo.education }},{{ cvInfo.profession }}
                        </div>
                        <div>学校经历: {{ cvInfo.profession }}</div>
                    </div>
                    <!-- <div class="ex1">
                    <div>学校名称|学历|专业名称:</div>
                    <div>学校经历:</div>
                    <div>经历时间:</div>
                </div> -->
                </div>
                <div class="socEx">
                    <div class="background"></div>
                    <div class="basic">社会经历</div>
                    <div class="ex1">
                        <div>工作年限：{{ cvInfo.workingYears }}</div>
                        <div>工作经历: {{ cvInfo.workExperience }}</div>
                    </div>
                </div>
                <div class="otherInfo">
                    <div class="background"></div>
                    <div class="basic">其它信息</div>
                    <div><span style="color:gray">技术技能：</span>{{ cvInfo.skill }}</div>
                    <div><span style="color:gray">所得荣誉：</span>{{ cvInfo.honor }}</div>
                    <div><span style="color:gray">个人评价：</span>{{ cvInfo.selfRecommendation }}</div>
                </div>
            </div>
            <div class="img" :style="{ display: bcolor == 1 ? 'block' : 'none' }">
                <div class="basicInfo">
                    <div class="background"></div>
                    <div class="basic">薪资预测</div>
                    <div style="margin-left: 20px;">{{ cvInfo.predictSalary }}</div>
                </div>
                <div class="eduEx">
                    <div class="background"></div>
                    <div class="basic">候选人标签</div>
                    <div class="basicTip" style="margin-bottom: 10px;">
                        <div class="title">基本标签</div>
                        <el-tag color="rgb(249, 240, 255)" style="color: rgb(175, 135, 229);margin-right: 5px;">{{
            cvInfo.age }}岁</el-tag>
                        <el-tag color="rgb(249, 240, 255)" style="color: rgb(175, 135, 229);"
                            v-if="cvInfo.politicalStatus">{{ cvInfo.politicalStatus }}</el-tag>
                    </div>
                    <div class="basicTip">
                        <div class="title">教育经历标签</div>
                        <el-tag color="rgb(230, 247, 255)" style="color: rgb(89, 176, 255);margin-right: 5px;">{{
            cvInfo.education }}</el-tag>
                        <el-tag color="rgb(230, 247, 255)" style="color: rgb(89, 176, 255);">{{ cvInfo.profession
                            }}</el-tag>
                    </div>
                    <div class="basicTip">
                        <div class="title">职位标签</div>
                        <el-tag color="rgb(230, 255, 251)" style="color: rgb(94, 216, 214);margin-right: 5px;">{{
            cvInfo.career }}</el-tag>
                    </div>
                    <div class="basicTip">
                        <div class="title">技能标签</div>
                        <el-tag color="rgb(255, 247, 230)" style="color: rgb(252, 191, 122);margin-right: 5px;">{{
            cvInfo.skill }}</el-tag>
                    </div>
                </div>
                <div class="otherInfo">
                    <div class="background"></div>
                    <div class="basic">能力分析</div>
                    <div id="echar" class="echar"></div>
                </div>
            </div>
            <div class="img" :style="{ display: bcolor == 2 ? 'block' : 'none' }">
                <div class="basicInfo">
                    <div style="margin-left: 20px;">{{ cvInfoTxt }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { reqUserResume, reqCollect, reqReumeTxt, reqUnCollect } from '@/api/index'
export default {
    name: 'analyzeResult',
    data() {
        return {
            value1: null,
            ycolor: 0,
            bcolor: 0,
            resumeId: 0,
            cvInfo: {},
            cvInfoTxt: {},
            isCollect: 0,
            isClick: false
        }
    },
    computed: {

    }
    ,
    methods: {
        async changeyColor() {
            console.log(this.cvInfo.isCollect);
            if (this.isClick) {
                alert('请勿频繁点击')
                return
            }
            this.isClick = true
            if (this.cvInfo.isCollect == 1) {
                const res = await reqUnCollect(this.resumeId)
                if (res.code == 1) {
                    this.getCv()
                    this.$message('取消收藏')

                }
            } else {
                const res = await reqCollect(this.resumeId)
                if (res.code == 1) {
                    this.getCv()
                    this.$message({
                        message: '收藏成功',
                        type: 'success'
                    });

                }
            }
            setTimeout(() => {
                this.isClick = false
            }, 2500);

        },
        changebColor0() {
            this.bcolor = 0
        },
        changebColor1() {
            this.bcolor = 1
        },
        changebColor2() {
            this.bcolor = 2
        },
        imgShow() {
            var chartDom = document.getElementById('echar');
            var myChart = this.$echarts.init(chartDom);
            var option;
            option = {
                radar: {
                    // shape: 'circle',
                    indicator: [
                        { name: '学历评分', max: 5 },
                        { name: '荣誉评分', max: 5 },
                        { name: '积极性评分', max: 5 },
                        { name: '技能评分', max: 5 },
                        { name: '工作年限评分', max: 5 }
                    ]
                },
                series: [
                    {
                        name: 'Budget vs spending',
                        type: 'radar',
                        data: [
                            {
                                value: [this.cvInfo.educationNumber, this.cvInfo.honorNumber, this.cvInfo.selfRecommendationNumber, this.cvInfo.skillNumber, this.cvInfo.workingYearsNumber],
                                name: 'Allocated Budget'
                            },
                        ]
                    }
                ]
            };

            option && myChart.setOption(option);

        },
        async getCv() {
            const res = await reqUserResume(this.resumeId)
            if (res.code == 1) {
                this.cvInfo = res.data
                this.imgShow()
            }
        },
        async getCvTxt() {
            const res = await reqReumeTxt(this.resumeId)
            if (res.code == 1) {
                this.cvInfoTxt = res.data
            }
        }
    },
    mounted() {
        this.resumeId = this.$route.params.id,
            this.getCv()
        this.getCvTxt()
    }
}
</script>

